.app-wrapper {
  display: grid;
  width: 1200px;
  margin: 0 auto;
  grid-template-areas:
    "header header header"
    "nav main main";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  /* gap: 10px; */
  height: 100vh;

}

.site-wrapper {
  /* background-color: #b8bbba; */
}

.app-wrapper-content {
  grid-area: main;
}

a {
  font-family: Lato;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  /* letter-spacing: 0em; */
  /* text-align: left; */
  color: #7E8EAB;
}

h1 {
  font-family: Lato;

}